// SECTION types :: START

.types {
  @include fluid(padding-top, 320px, 1439px, 55px, 101px);
  @include fluid(padding-bottom, 320px, 1439px, 25px, 50px);

  &__title {
    text-align: center;
    line-height: 1;
    font-weight: 400;
    font-family: "Muli", sans-serif;
    @include fluid(font-size, 320px, 1600px, 25px, 40px);
    @include fluid(padding-bottom, 320px, 1439px, 35px, 70px);
  }

  &__block {
    width: 100%;
    max-width: 320px;
    min-height: 498px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    background: $c-white;
    box-shadow: 0 15px 35px 0 rgba(0,68,157,0.07);

    &-wrapper {
      display: flex;
      align-items: flex-start;
      @include respond(max-small) {
        flex-wrap: wrap;
      }

      > div {
        width: 100%;
        max-width: calc(100% / 3);
        padding-left: 5px;
        padding-right: 5px;
        @include respond(max-small) {
          max-width: 100%;
          padding-top: 15px;
          padding-bottom: 15px;
        }
      }
    }

    &-top {
      min-height: calc(498px - 210px);
      padding: 40px 50px;
      @include respond(max-large) {
        padding-left: 40px;
        padding-right: 40px;
      }
      @include respond(max-medium) {
        padding-left: 30px;
        padding-right: 30px;
      }
    }

    &-bottom {
      height: 210px;
      padding-top: 40px;
      padding-bottom: 40px;
      border-top: 2px solid #F8F8F8;
    }

    &-icon {
      display: flex;
      justify-content: center;
      min-height: 62px;
    }

    &-name {
      padding-top: 28px;
      padding-bottom: 20px;
      color: $c-black;
      text-align: center;
      font-weight: bold;
      @include fluid(font-size, 320px, 1430px, 22px, 26px);
    }

    &-desc {
      color: rgba($c-black, 0.4);
      text-align: center;
      @include fluid(font-size, 320px, 1430px, 15px, 17px);
    }

    &-limit {
      padding-bottom: 40px;
      color: rgba($c-black, 0.4);
      text-align: center;
      font-size: 17px;
      @include fluid(font-size, 320px, 1430px, 15px, 17px);

      span {
        color: $c-black;
      }
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 185px;
      height: 80px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 10px;
      border: 2px solid #CD084B;
      background-color: transparent;
      color: #CD084B;
      letter-spacing: 1.35px;
      text-transform: uppercase;
      font-size: 18px;
      font-weight: bold;
      @include transition(background-color, color);

      &:hover {
        background-color: #CD084B;
        color: $c-white;
        @include transition(background-color, color);
      }
    }
  }
}

// SECTION types :: END